let _this = this;
import {request} from '@/api/reques.js'
import {goWindow} from '@/api/navigate.js'  // 页面跳转
export default{
	props:{
		carList:{
			type:Array
		},
		shopId:{
			type:Number
		}
	},
	data(){
		return{
			target:'',
			show1:true,
			show2:false,
			show3:false,
			carLists:[],
			list:[],
			page_index1:1,
			page_index2:1,
			page_count1:0,
			page_count2:0
		}
	},
	created() {
		_this = this;
	},
	watch:{
		carList(arr){
			_this.list = arr
			_this.carLists = arr
		}
	},
	methods:{
		clickList(e){
			if(!e.target.dataset.id)return;
			let target = e.target.dataset.id;
			this.target = target;
			console.log(this.target)
			switch(this.target){
				case "one":
					this.show1 = true
					this.show2 = false
					this.show3 = false
					this.carLists = this.list
					break;
				case "two":
					this.show2 = true
					this.show1 = false
					this.show3 = false
					this.getStoreCarList();
					break;
				case "three":
					this.show3 = true
					this.show2 = false
					this.show1 = false
					this.getShopDealCarList()
					break;
				default:
					this.show1 = false
					this.show2 = false
					this.show3 = false
			}
		},
		handleErr(e,index){
			if(e.type=='error'){
				this.carList[index].pic_cover = "../../../static/images/list.jpg"
			}	
		},
		getStoreCarList(page_index1 = 1){
			this.carLists = [];
			request("/shop/getShopCarGoodsList",{
				shop_id:this.shopId,
				page_index:page_index1,
				page_size:10,
				is_recommend:1
			},"POST")
			.then((res)=>{
				let data = res.data.data.data;
				if(page_index1 > 1){
					data = this.carList.concat(data)
				}
				this.carLists = data;
				this.page_count1 = res.data.data.page_count;
			})
		},
		getShopDealCarList(page_index2 = 1){
			this.carLists = [];
			request("/shop/getShopDealCarGoodsList",{
				shop_id:this.shopId,
				page_index:page_index2,
				page_size:10,
			},"POST")
			.then((res)=>{
				let data = res.data.data.data;
				if(page_index2 > 1){
					data = this.carList.concat(data)
				}
				data.forEach((val) => {
					if(val.goods_id !== null || val.goods_id == ""){
						_this.carLists.push(val)
					}
				})
				this.page_count2 = res.data.data.page_count;
			})
		},
		handleDetail(goodsId,group,promotion_id){
			let groupId = parseInt(String(group).substr(0,1));
			this.$c.detailsJump(goodsId,groupId,promotion_id)
		},
		loadData() {
			let _this = this;
			if(show2){
				if (_this.page_index1 < _this.page_count1) {
					_this.page_index1++;
					_this.getStoreCarList(_this.page_index1);
				}else{
					return
				}
			}else if(show3){
				if (_this.page_index2 < _this.page_count2) {
					_this.page_index2++;
					_this.getShopDealCarList(_this.page_index2);
				}else{
					return
				}
			}
		}
	}
}
